import React from 'react';
import styled from 'styled-components';

const Content = styled.div`
  margin: 10px auto 10px auto;
  width: 90%;
  flex-grow: 1;
  border: 1px solid #e95312;
  border-radius: 1em;
`;

const Card = () => {
  return <Content></Content>;
};

export default Card;
